<template>
  <el-card class="box-card">
    <el-button type="primary" @click="$router.push('/admin/main/delivery/add-ingredient')">Add Ingredient</el-button>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="barcode">
        <el-input placeholder="barcode" v-model="param.barcode"></el-input>
      </el-form-item>
      <el-form-item label="iname">
        <el-input placeholder="iname" v-model="param.iname"></el-input>
      </el-form-item>
      <el-form-item label="weight">
        <el-input placeholder="weight" v-model="param.weight"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="barcode"
          prop="barcode"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="iname"
          label="iname"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="weight"
          label="weight"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="operation"
      >
          <template v-slot="scope">
            <el-button type="danger" size="small" @click="handleDelete(scope.row.barcode)">Delete</el-button>
          </template>
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        barcode: '',
        iname: '',
        weight: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList(){
      this.$axios.post('/ingredients/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm(){
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e){
      this.pageInfo.pageNum = e
      this.getTableList()
    },
    handleDelete(barcode){
      this.$confirm('Are you sure to delete this ingredient?', 'Warning', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.$axios.get('/ingredients/remove/' + barcode).then(() => {
          this.$message({
            type: 'success',
            message: 'Delete successfully!'
          });
          this.getTableList();
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: 'Delete canceled'
        });
      });
    }
  },

}
</script>

<style scoped lang="scss">
  .demo-form-inline, .el-pagination {
    margin-top: 10px;
  }
</style>
